<template>
  <div>
    <ul>
      <li v-for="m in messageList" :key="m.id">
        <!-- <router-link :to="`/home/message/detail?id=${m.id}&title=${m.title}`">{{m.title}}</router-link>&nbsp;&nbsp; -->
        <router-link :to="{
          name: 'detail',
          query: {
            id: m.id, 
            title: m.title
          }
        }" replace>{{m.title}}</router-link>&nbsp;&nbsp;
        <button @click="pushShow(m)">push查看</button>
        <button @click="replaceShow(m)">replace查看</button>
      </li>
      <hr>
      <router-view></router-view>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'myMessage',
  data() {
    return {
      messageList: [
        { id: '001', title: '消息001' },
        { id: '002', title: '消息002' },
        { id: '003', title: '消息003' },
      ]
    }
  },
  methods: {
    pushShow(m) {
      this.$router.push({
        name: 'detail',
          query: {
            id: m.id, 
            title: m.title
          }
       })
    },
    replaceShow(m) {
      this.$router.replace({
        name: 'detail',
          query: {
            id: m.id, 
            title: m.title
          }
       })
    }
  }
}
</script>

